import { TranslateService } from '@ngx-translate/core';
import { ProfileService } from './services/profile.service';
import { RouterConstants } from './../../shared/config/constants/router.constants';
import { GlobalDataService } from './../../shared/services/globalData/global-data.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertService } from 'src/app/shared/services/alert/alert.service';
import { UserSession } from 'src/app/shared/models/user.model';
import { GamePlay, Player } from 'src/app/shared/models/game_play.model';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {

  profileOptions = [
    { name: 'ORDERS', icon: 'bag-handle-outline', url: RouterConstants.PROFILE_ORDERS },
    { name: 'ACTIVITY', icon: 'game-controller-outline', url: RouterConstants.PROFILE_ACTIVITY },
    { name: 'CONFIGURATION', icon: 'cog-outline', url: RouterConstants.PROFILE_CONFIGURATION },
    { name: 'ABOUT', icon: 'information-outline', url: RouterConstants.PROFILE_ABOUT },
  ];
  loading: boolean;
  constructor(
    public globalData: GlobalDataService,
    private router: Router,
    private alertService: AlertService,
    private profileService: ProfileService,
    private translate: TranslateService,
  ) { }

  ngOnInit() {
  }

  navigate(url: string) {
    this.router.navigateByUrl(url);
  }

  async logout() {
    try {
      this.loading = true;
      const buttons = [
        { text: this.translate.instant('ALERTS.CANCEL'), role: 'cancel', },
        { text: this.translate.instant('ALERTS.ACCEPT'), role: 'accept' }
      ];
      let result: any = await this.alertService.showGameAlert(this.translate.instant('PAGES.PROFILE.LOGOUT_TITLE'), this.translate.instant('PAGES.PROFILE.LOGOUT_DESCRIPTION'), buttons);
      if (result && result.data && result.role === 'accept') {
        await this.profileService.logout().toPromise();
        this.globalData.userSession = new UserSession();
        this.globalData.gamePlay = new GamePlay();
        this.globalData.player = new Player();
        this.router.navigateByUrl(RouterConstants.LOGIN, { replaceUrl: true });
        await this.globalData.removeSession();
      }
      this.loading = false;
    } catch (error) {
      this.loading = false;
      console.log("Error", error);
    }
  }

}
