import { HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { RequestEndpoints } from 'src/app/shared/config/constants/api.constants';
import { ApiService } from 'src/app/shared/services/api/api.service';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ProfileService {

  constructor(
    private apiService: ApiService,
  ) { }

  getOrderListUser(currentElement, elementsPerPage): Observable<any>{
    let httpParams = new HttpParams();
    httpParams = httpParams.append('currentElement', currentElement);
    httpParams = httpParams.append('elementsPerPage', elementsPerPage);
    return this.apiService.get(environment.API_URL + RequestEndpoints.GET_PROFILE_ORDERS, httpParams).pipe(
      map(async (data: any) => {
        data.rows.forEach(element => {
          element.updatedAt = new Date(element.updatedAt);
          element.orderDate = new Date(element.orderDate);
        });
        return data;
    }),catchError(error => throwError(error)));
  }

  getOrderUser(id): Observable<any>{
    let httpParams = new HttpParams();
    httpParams = httpParams.append('id', id);
    return this.apiService.get(environment.API_URL + RequestEndpoints.GET_PROFILE_ORDER, httpParams).pipe(
      map(async (data: any) => {
        data.updatedAt = new Date(data.updatedAt);
        data.orderDate = new Date(data.orderDate);
        return data;
    }),catchError(error => throwError(error)));
  }

  getActivityListUser(currentElement, elementsPerPage): Observable<any>{
    let httpParams = new HttpParams();
    httpParams = httpParams.append('currentElement', currentElement);
    httpParams = httpParams.append('elementsPerPage', elementsPerPage);
    return this.apiService.get(environment.API_URL + RequestEndpoints.GET_PROFILE_ACTIVITY, httpParams).pipe(
      map(async (data: any) => {
        data.rows.forEach(element => {
          element.endPlay = new Date(element.endPlay);
        });
        return data;
    }),catchError(error => throwError(error)));
  }

  createComment(text: string): Observable<any>{
    let httpParams = new HttpParams();
    httpParams = httpParams.append('text', text);
    return this.apiService.post(environment.API_URL + RequestEndpoints.COMMENTS, httpParams).pipe(
      map(async (data: any) => {
        return data;
    }),catchError(error => throwError(error)));
  }

  getRemindersConf(): Observable<any>{
    let httpParams = new HttpParams();
    return this.apiService.get(environment.API_URL + RequestEndpoints.GET_PROFILE_REMINDERS, httpParams).pipe(
      map(async (data: any) => { return data;
    }),catchError(error => throwError(error)));
  }

  saveRemindersConf(days, hours): Observable<any>{
    let httpParams = new HttpParams();
    httpParams = httpParams.append('days', JSON.stringify(days));
    httpParams = httpParams.append('hours', JSON.stringify(hours));
    return this.apiService.post(environment.API_URL + RequestEndpoints.GET_PROFILE_REMINDERS, httpParams).pipe(
      map(async (data: any) => { return data;
    }),catchError(error => throwError(error)));
  }

  logout(): Observable<any>{
    let httpParams = new HttpParams();
    return this.apiService.post(environment.API_URL + RequestEndpoints.LOGOUT, httpParams).pipe(
      map(async (data: any) => { return data;
    }),catchError(error => throwError(error)));
  }

}
